import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"

const ProductEmbeddedWizard = () => (
  <Layout>
    <Seo title="Embedded Wizard" />
    <main>
      <header>
        <div className="container px-5">
          <div className="row justify-content-center">
            <div className="col-lg-8 col-xxl-6">
              <div className="text-center my-5">
                <h1 className="fw-bolder mb-3">Embedded Wizard</h1>
                <p className="lead fw-normal text-muted mb-4">
                  革新的な GUIソリューション開発ツール
                </p>
              </div>
            </div>
          </div>
        </div>
      </header>
      {/* Page Content (Start) */}
      <section className="bg-dark">
        <div className="container">
          <div className="row gx-5 justify-content-center">
            <div className="col-lg-8">
              <div className="row py-lg-5 py-4">
                <img
                  className="img-fluid rounded-3"
                  src="/images/product_embedded_wizard/stm32f750-discovery-1.png"
                  alt="stm32f750-discovery-1"
                />
              </div>
            </div>
            <div className="col-lg-4">
              <div className="row py-lg-5 py-4">
                <h1
                  className="py-lg-5 text-center text-lg-start"
                  style={{
                    color: "#fff",
                    textShadow: "0 0 20px rgba(255,255,255,0.5)",
                  }}
                >
                  組み込みシステムでも
                  <br />
                  美しいUIを表現できる。
                  <br />
                  効率良く開発できる。
                  <br />
                </h1>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* Features Section (Start) */}
      <section className="py-lg-5" id="features">
        <div className="container px-5 my-5">
          <div className="row gx-5">
            <div className="col-lg-4 mb-5 mb-lg-0">
              <h2 className="fw-bolder text-gradient">
                Embedded Wizard Studio
              </h2>
              <h4 className="mt-lg-5">
                GUI開発に特化された
                <br />
                統合開発環境
              </h4>
            </div>
            <div className="col-lg-8">
              <div className="row gx-5 row-cols-1 row-cols-md-2">
                <div className="col mb-5 h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-collection" />
                  </div>
                  <h2 className="h5 fw-bold">WYSIWYGエディター</h2>
                  <p className="mb-0">
                    開発PCのディスプレイ画面で見たものが、そのままANSI
                    Cソースコード生成。様々なCコンパイラに対応。
                  </p>
                </div>
                <div className="col mb-5 h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-building" />
                  </div>
                  <h2 className="h5 fw-bold">コンポーネントベースの開発</h2>
                  <p className="mb-0">
                    すべての要素をコンポーネントとして扱える！保守性・拡張性に優れたUI開発ができる。
                  </p>
                </div>
                <div className="col mb-5 mb-md-0 h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-toggles2" />
                  </div>
                  <h2 className="h5 fw-bold">強力な実行環境</h2>
                  <p className="mb-0">
                    優れた実行環境のおかげで、開発PC上で実行・デバッグまで一つのツールで完結。
                  </p>
                </div>
                <div className="col h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-toggles2" />
                  </div>
                  <h2 className="h5 fw-bold">豊富なサンプル提供</h2>
                  <p className="mb-0">
                    完成度の高いサンプルが30個以上！サンプルを動かしながら学べる。
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* Page Content (Start) */}
      <section className="bg-light">
        <div className="container">
          <div className="row gx-5 justify-content-center">
            <div className="col-lg-8">
              <div className="row py-lg-5 py-4">
                <img
                  className="img-fluid rounded-3"
                  src="/images/product_embedded_wizard/embedded-wizard-studio.png"
                  alt="stm32f750-discovery-1"
                />
              </div>
            </div>
            <div className="col-lg-4">
              <div className="row py-lg-5 py-4">
                <h1 className="py-lg-5 text-center text-lg-start">
                  PCにて完成。
                  <br />
                  ソースコードをビルドして
                  <br />
                  書き込むだけ。
                  <br />
                </h1>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* Features section (Start) */}
      <section className="py-lg-5" id="features">
        <div className="container px-5 my-5">
          <div className="row gx-5">
            <div className="col-lg-4 mb-5 mb-lg-0">
              <h2 className="fw-bolder text-gradient">Platform Package</h2>
              <h4 className="mt-lg-5">
                組み込みOS
                <br />
                組み込みLinux
                <br />
                Webブラウザまで対応。
              </h4>
            </div>
            <div className="col-lg-8">
              <div className="row gx-5 row-cols-1 row-cols-md-2">
                <div className="col mb-5 h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-collection" />
                  </div>
                  <h2 className="h5 fw-bold">多数のMCU対応</h2>
                  <p className="mb-0">
                    STM32シリーズ、i.MXシリーズ、LPCXpressoシリーズ
                    <br />
                    RZシリーズ、ESP32シリーズ、PIC32シリーズ対応。
                  </p>
                </div>
                <div className="col mb-5 h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-building" />
                  </div>
                  <h2 className="h5 fw-bold">様々なOS対応</h2>
                  <p className="mb-0">
                    ITRON、FreeRTOS、RTX5、ThreadX、VxWorks対応。
                    <br />
                    組み込みOS、ベアメタルもオーケー。
                  </p>
                </div>
                <div className="col mb-5 mb-md-0 h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-toggles2" />
                  </div>
                  <h2 className="h5 fw-bold">組み込みLinux対応</h2>
                  <p className="mb-0">OpenGL、フレームバッファをサポート</p>
                </div>
                {/* <div className="col h-100">
                  <div className="feature bg-primary bg-gradient text-white rounded-3 mb-3">
                    <i className="bi bi-toggles2" />
                  </div>
                  <h2 className="h5 fw-bold"></h2>
                  <p className="mb-0">
                  </p>
                </div> */}
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* Page Content (Start) */}
      <section className="bg-light">
        <div className="container">
          <div className="row gx-5 justify-content-center">
            <div className="col-lg-8">
              <div className="row py-lg-5 py-4">
                <img
                  className="img-fluid rounded-3"
                  src="/images/product_embedded_wizard/android.png"
                  alt="stm32f750-discovery-1"
                />
              </div>
            </div>
            <div className="col-lg-4">
              <div className="row py-lg-5 py-4">
                <h1 className="py-lg-5 text-center text-lg-start">
                  デバイス
                  <br />
                  プラットフォーム
                  <br />
                  OSを問わない。
                </h1>
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
  </Layout>
)

export default ProductEmbeddedWizard
